<template>
	<view class="content">
		<!-- 自定义组件 -->
		<navbar></navbar>
		<tab :list="tabList"></tab>
		
		<view class="bodyScroll">
			<scroll-view class="list-scroll" scroll-y="true">
				<view>
					<view v-for="item in 100" :key="item">{{item}}</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	
	export default {
		data() {
			return {
				title: 'Hello',
				tabList:[],
			}
					
		},
		onLoad() {
			this.getLabel();
		},
		methods: {
		
			getLabel() {
				console.log(this.$api)
				this.$api.get_label({
					name:'get_label'
				}).then(res => {
					console.log(res)
					const {data} = res;
					this.tabList = res.data;
				})
			}
		}
	}
</script>

<style>
	

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
	page{
		height:100%;
		display: flex;
	}	
	.content{
		display: flex;
		flex-direction: column;
		flex:1;
		border:1px solid red;
		box-sizing: border-box;
		.navbar{
			display: flex;
		}
		.bodyScroll{
			flex:1;
			overflow: hidden;
			box-sizing: border-box;
			.list-scroll{
				flex:auto;
				height:100%;
				display: flex;
				flex-direction: column;
			}
		}
	}
</style>
